import React, { useEffect, useState } from "react";
import { Bar } from "react-chartjs-2";
import Chart from "chart.js/auto";

const GraficaCalificaciones = () => {
  const [alumnos, setAlumnos] = useState([]);

  useEffect(() => {
    const obtenerDatos = async () => {
      try {
        const response = await fetch("https://alex.starcode.com.mx/apiAlumnos.php");
        const data = await response.json();
        setAlumnos(data);
      } catch (error) {
        console.error("Error fetching data:", error);
      }
    };

    obtenerDatos();

    const interval = setInterval(obtenerDatos, 5000); // Actualizar cada 5 segundos

    return () => clearInterval(interval);
  }, []);

  const generarDatosGrafica = (calificaciones) => {
    const labels = Object.keys(calificaciones); // Claves de las prácticas
    const valores = Object.values(calificaciones); // Valores de las calificaciones

    return {
      labels: labels,
      datasets: [
        {
          label: "Calificaciones",
          data: valores,
          backgroundColor: "rgba(75, 192, 192, 0.2)",
          borderColor: "rgba(75, 192, 192, 1)",
          borderWidth: 1,
        },
      ],
    };
  };

  const calcularPromedio = (calificaciones) => {
    const total = Object.values(calificaciones).reduce((acc, curr) => acc + parseFloat(curr), 0);
    return total / Object.values(calificaciones).length;
  };

  return (
    <div style={styles.container}>
      <h2 style={styles.header}>Gráficas de Calificaciones por Alumno</h2>
      {alumnos.map((alumno) => {
        const calificaciones = alumno.practicas;
        const promedio = calcularPromedio(calificaciones);

        return (
          <div key={alumno.id} style={styles.alumnoContainer}>
            <h3 style={styles.alumnoHeader}>{alumno.nombre} - Promedio: {promedio.toFixed(2)}</h3>
            <Bar data={generarDatosGrafica(calificaciones)} options={graficaOptions} />
          </div>
        );
      })}
    </div>
  );
};

const graficaOptions = {
  responsive: true,
  scales: {
    y: {
      beginAtZero: true,
    },
  },
};

const styles = {
  container: {
    maxWidth: "900px",
    margin: "0 auto",
    padding: "20px",
    backgroundColor: "#0a0a0a",
    border: "4px solid #ff00ff",
    borderRadius: "20px",
    boxShadow: "0 0 15px #00ffff, 0 0 30px #ff00ff, inset 0 0 10px #222",
    color: "#00ff99",
    textAlign: "center",
    transition: "box-shadow 0.3s ease-in-out",
  },
  header: {
    color: "#00ff99",
    fontSize: "2.5em",
    textShadow: "2px 2px #ff00ff, 4px 4px #00ffff",
    marginBottom: "20px",
    textTransform: "uppercase",
  },
  alumnoContainer: {
    marginBottom: "40px",
    padding: "20px",
    background: "linear-gradient(145deg, #1a1a1a, #333)",
    borderRadius: "10px",
    border: "2px solid #ff00ff",
    boxShadow: "0 5px 15px rgba(0, 0, 0, 0.7), 0 0 10px #ff00ff, 0 0 20px #00ffff",
    fontFamily: "'Orbitron', sans-serif",
    color: "#00ff99",
    textAlign: "left",
    position: "relative",
  },
  alumnoHeader: {
    color: "#00ff99",
    fontSize: "1.8em",
    marginBottom: "20px",
  },
};

export default GraficaCalificaciones;
